body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.header {
  background: #42275a;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #734b6d, #42275a);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#734b6d), to(#42275a));
  background: -webkit-linear-gradient(left, #734b6d, #42275a);
  background: -o-linear-gradient(left, #734b6d, #42275a);
  background: linear-gradient(to right, #734b6d, #42275a); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */   
  padding: 60px 0;
  margin: 0 0 40px 0;
}

.header__title {
  font-family: 'Playfair Display', serif;
  font-size: 24px;
  color: white;
}

.header__subtitle {
  font-family: 'Lato', sans-serif;
  font-size: 20px;
  color: white;
}

.button-primary {
  -webkit-box-shadow: none;
          box-shadow: none;
  border: none;
  color: #42275a;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  border-radius: 40px;
  border: 2px solid #42275a;
  padding: 8px 24px;
  cursor: pointer;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 1px;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .button-primary {
    display: block;
    margin: 0 auto 40px auto;
  }
}


.button-primary:hover {
  background: #42275a;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #734b6d, #42275a);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#734b6d), to(#42275a));
  background: -webkit-linear-gradient(left, #734b6d, #42275a);
  background: -o-linear-gradient(left, #734b6d, #42275a);
  background: linear-gradient(to right, #734b6d, #42275a); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ 
  color: white;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.button-primary:active, .button-primary:focus {
  outline: none;
}


